@import "_base/settings";
@import "_base/components";
@import "_base/components/items";

body {
	width: 100%;
}

.page-compare {
	color: #000;
	font-size: 14px;
	font-weight: 400;
	line-height: lh(14, 26);
	padding-top: 27px;
	padding-bottom: 52px;


	.top-section {
		margin-bottom: 50px;
		h2 {
			color: #000;
			font-size: 32px;
			font-weight: 600;
			line-height: 26px; /* 81.25% */
		}
	}

	.top-section-sub {
		margin-bottom: 20px;
		span {
			padding-left: 18px;
		}
		padding-bottom: 15px;
		border-bottom: 1px solid #EAEAEA;
		.link {
			color: #06F;
			padding-left: 19px;
		}

		&-del {
			display: flex;
			gap: 15px;
			align-items: center;
			margin-bottom: 103px;
			span {
				display: flex;
				align-items: center;
				gap: 12px;
				.icon  {
					display: flex;
					align-items: center;
					gap: 4px;
					&::before {
						width: 20px;
						height: 20px;
						background: url("../img/icon/delete_compere.svg");
						content: '';
					}


				}
			}
		}
	}

	&__list {
		display: flex;
		gap: 49px;
	}

	.item {
		max-width: 175px;
		&__top {
			display: flex;

			gap: 14px;
			margin-bottom: 8px;
			img {
				max-width: 100px;
			}

			&__icon {
				display: flex;
				align-items: center;
				flex-direction: column;
				gap: 11px;
			}

			&__icon--favorites {
				width: 14px;
				height: 13px;
				background: url("../img/icon/compare-favorites.svg");
				&:hover {
					background: url("../img/icon/compare-favorites-hover.svg");
				}
			}
			&__icon-del {
				width: 20px;
				height: 20px;
				background: url("../img/icon/delete_compere.svg");
				&:hover {
					background: url("../img/icon/delete_compere-hover.svg");
				}
			}
		}


		&__body {
			padding-left: 34px;
		}

		&__name {
			color: #3C9DDC;
			font-size: 14px;
			font-weight: 500;
			line-height: 20px; /* 142.857% */
		}
		&__art {
			color: #666;
			font-size: 14px;
			font-weight: 400;
			line-height: 26px; /* 185.714% */
			margin-bottom: 3px;
			span {
				color: #999;
				padding-left: 2px;
			}
		}
		&__price {

			color: #000;
			font-size: 15px;
			font-weight: 700;
			line-height: 26px; /* 173.333% */
		}

	}




}

.header {
	&--fixed {
		position: fixed;
		top: 0;
		z-index: 50;
		background-color: #fff;
		width: 100%;
		.header__row1 {
			display: none;
		}
	}

	&__row1 {
		background: #484848;
		color: #fff;
		font-size: 13px;
		letter-spacing: .3px;

		.container {
			display: flex;
			align-items: center;
			min-height: 31px;
			justify-content: space-between;
			ul {
				display: flex;
				gap: 20px;
			}
		}
	}

	&__row2 {
		border-bottom: 1px solid #DFDFDF;
		position: relative;
		box-shadow: -3px 2px 15px 1px rgba(0, 0, 0, 0.05);

		.container {
			display: flex;
			align-items: center;
			min-height: 84px;

		}



		&-search {
			height: 50px;
			margin-left: 18px;
			display: flex;
			flex-grow: 1;
			.placeholder-container {
				width: 100%;
				color: #CCC;
				position: relative;
				.btn {
					position: absolute;
					top: 5px;
					right: 5px;
					width: 40px;
					min-height: 40px;
					max-height: 40px;
					z-index: 2;
					border-radius: 2px;
					display: flex;
					align-items: center;
					justify-content: center;
					&::before {
						display: block;
						min-width: 19px;
						min-height: 19px;
						background: url("../img/icon/search.svg");
						content: '';

					}

				}

				input {
					border-radius: 5px;
					border: 2px solid #DEDEDE;
				}
			}
		}

		&-right {
			min-height: 40px;
			margin-left: 48px;
			display: flex;
			gap: 23px;
			font-size: 13px;
			font-weight: 400;
			color: #494F54;
			p {
				line-height: 1;
			}


			&-price {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				position: relative;
				cursor: pointer;


				&-caption {
					background: #B4DFFB;
					border-radius: 3px;
					font-size: 12px;
					font-weight: 700;
					line-height: lh(11, 22);
					text-align: center;
					position: relative;
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						top: 0;
						right: 0;
						max-height: 20px;
						transform: translate(50%, -50%);
						border: 2px solid #FFFFFF;
						font-size: 10px;
						padding: 0 5px;
						color: #fff;
						line-height: 22px;
						font-weight: bold;
						border-radius: 100%;
						background-color: #FB1A1A;
					}
				}





			}

			&-favourites {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;

				&-icon {
					position: relative;
					&::before {
						display: block;
						width: 21px;
						height: 18px;
						background: url("../img/icon/heart.svg");
						content: '';
					}
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						top: 0;
						right: 0;
						max-height: 20px;
						transform: translate(50%, -50%);
						background: #FB1A1A;
						border: 2px solid #FFFFFF;
						font-size: 10px;
						padding: 0 5px;
						color: #fff;
						line-height: 22px;
						font-weight: bold;
						border-radius: 100%;
					}
				}
				p {
					line-height: 1;
				}
			}

			&-user {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;

				&-icon {
					position: relative;
					&::before {
						display: block;
						width: 19px;
						height: 21px;
						background: url("../img/icon/user.svg");
						content: '';
					}
				}
				p {
					line-height: 1;
				}
			}

			&-cart {
				position: relative;
				cursor: pointer;
				align-self: flex-start;
				margin-left: 36px;
				min-height: 40px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				text-align: center;

				&:hover {

					&::after {
						transform: rotate(0);
					}

					& > .popup-cart-list {
						transform: rotateX(0);
						visibility: visible;
						opacity: 1;
						z-index: 1000;
					}
				}

				&-caption {
					&.active {
						background-color: #F33144;
					}
					padding: 0 9px;
					background: #3E4347;
					border-radius: 3px;
					color: #FFF;
					font-size: 12px;
					font-style: normal;
					font-weight: 700;
					line-height: lh(12,22);

					display: flex;
					align-items: center;
					gap: 8px;

					&::after {

						line-height: 1;
						position: relative;
						width: 11px;
						height: 7px;
						background: url("../img/icon/arr/arr.svg");
						content: '';
						transform: rotate(180deg);
						transition: .3s;
					}


				}


			}




		}

	}


}

.sub-header {
	align-items: center;
	padding-top: 15px;
	padding-bottom: 20px;
	display: flex;
	gap: 22px;

	.btn {
		height: 47px;
		padding: 0 26px 0 13px;
		gap: 10px;
		align-items: center;
		border-radius: 10px;
		display: inline-flex;
		font-weight: 500;
		font-size: 14px;
		&--spes {
			background-color: #FFCB00;
			font-style: normal;
			&::before {
				width: 23px;
				height: 23px;
				background: url("../img/icon/money.svg");
				content: '';
			}

		}
		&--sale {
			background-color: #42ADE2;
			color: #fff;
			&::before {
				width: 25px;
				height: 34px;
				background: url("../img/icon/lightning.svg");
				content: '';

			}
		}
		&--new {
			background-color: #C7E755;
			&::before {
				width: 23px;
				height: 25px;
				background: url("../img/icon/siren.svg");
				content: '';
			}
		}
		&--ball {
			background-color: #B4DFFB;
			position: relative;
			font-weight: 700;
			span {
				position: absolute;
				top: -8px;
				right: -24px;
				background: #FF5656;
				border-radius: 15px;
				color: #fff;
				font-size: 14px;
				line-height: lh(14, 22);
				padding: 0 10px;
				display: block;
			}
			&::before {
				width: 32px;
				height: 32px;
				background: url("../img/icon/ball.svg");
				content: '';
			}
		}
	}
}

.footer {
	padding: 35px 0 45px;
	background: #484848;
	font-weight: 400;
	font-size: 14px;
	line-height: lh(14, 30);
	color: #EAEAEA;
	&-row1 {
		display: flex;
		justify-content: space-between;
		h3 {
			font-weight: 500;
			font-size: 16px;
			line-height: lh(16, 22);
			margin-bottom: 12px;
		}
	}
}

.banner {
	margin-bottom: 55px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #ECECEC;

}

.breadcrumps {
	color: #999;
	display: flex;
	gap: 3px;
	line-height: lh(14,22);
	margin-bottom: 39px;
	a {
		text-decoration: underline;

		&:nth-child(1) {
			text-decoration: none;
		}
	}



}


