.page-lk--revert {

	.lk-field__body-categories {
		gap: 17px;
		justify-content: flex-end;
	}


	.lk-field__body-categories-name {
		flex: 0 0 auto;
		padding-right: 14px;
	}
	.lk-field__body-categories-item {
		color: #fff;
		font-size: 12px;
		text-align: center;
		position: relative;


	}

	.lk-field__body-categories-item.close {
		background-color: #B4DFFB;
		color: #000;
	}

	.lk-field-table__title {
		grid-template-columns: 22px 1fr 81px 152px 92px 60px;
	}
	.lk-field-table__item {
		grid-template-columns: 22px 1fr 81px 152px 92px 60px;
		padding: 12px 0;
	}
	.th-caption {
		display: flex;
		gap: 7px;
		font-size: 12px;
		p {

			min-width: 95px;
		}
		&__descr {
			padding: 3px 11px;
			border-radius: 5px;
			background: #878787;
			max-width: 288px;
			color: #fff;
			line-height: 20px;
		}

	}
}
