.page-catalog-list {
	.breadcrumps {
		align-items: flex-end;
		a:first-child {
			align-items: center;
			font-size: 32px;
			font-weight: 600;
			color: #000;
			display: flex;
			gap: 15px;
			line-height: 1;
			margin-right: 15px;
		}
	}

}



.breadcrumps {
	a {
		align-items: center;
		font-size: 14px;
		font-weight: 400;
		display: flex;
		gap: 15px;
		line-height: 22px;
		margin-right: 15px;
		color: #666;
	}
	margin-bottom: 26px;
	display: flex;
}

.page-catalog-list {

	.top-part {



	}

	.body {
		display: flex;
		gap: 20px;
		&__filter {
			width: 230px;
			h3 {
				font-size: 14px;
				font-weight: 600;
				line-height: lh(14,26);
				margin-bottom: 14px;
			}
			&-wrapper {
				height: 90px;
				overflow: hidden;
			}
			&.show {
				.body__filter-wrapper {
					height: auto;
				}
			}
			&-btns {
				margin-top: 20px;
				display: flex;
				gap: 10px;
				justify-content: space-between;
				cursor: initial;
				.btn {
					width: 50%;
				}

			}

			&-link {
				color: #158AE0;
				font-size: 14px;
				font-style: normal;
				font-weight: 500;
				line-height: 26px;
				margin-top: 12px;
				margin-bottom: 9px;
				cursor: pointer;
			}

		}
		&__list {

			flex-grow: 1;

			.item-product-line {
				&:first-child {
					border-top: 1px solid #EAEAEA;
				}
				&__spec-box {
					&__quan-box {
						width: 216px;
						.btn {
							background-color: #FFCB00;
						}
					}
				}



			}
		}
	}

	&.product-list--column {
		.body {
			justify-content: space-between;
			&__list {
				display: grid;
				grid-template-columns: repeat(4, 217px);
				justify-content: space-between;
				margin-left: 20px;
				flex-grow: 1;
				gap: 20px;
				margin-bottom: 40px;
				.item-product-line {
					flex-direction: column;
					border: none;
					// max-width: 217px;
					padding: 0;

					&__spec-box {
						margin-left: initial;
					}

					&__img-box {
						width: 100%;
						margin-bottom: 26px;
						margin-left: initial;
					}

					&__descr-box {
						margin: 0;
					}

					&__descr-box__artical {
						order: -1;
						margin-bottom: 3px;
					}
					&__descr-box__text {
						font-size: 14px;
						margin-bottom: 10px;
					}
					&__descr-box__stock-box {
						font-size: 13px;
						margin-bottom: 13px;
					}
					&__spec-box__quan-box {
						justify-content: space-between;
					}




				}



			}
		}

		.top-bar__select-view__column {
			background: url("../img/icon/top-bar/column.svg");
		}

		.top-bar__select-view__row {
			width: 21px;
			height: 15px;
			background: url("../img/icon/top-bar/row-silver.svg");
		}
	}



}

.top-bar {

	min-height: 30px;
	display: flex;
	align-items: center;
	margin-bottom: 30px;

	&__find{
		color: #666;
	}

	.checkbox {
		margin-left: auto;
	}

	.select {
		margin-left: 20px;
		font-size: 14px;
		font-weight: 400;
		&__header {
			border-radius: 5px;
			background: #FFF;
			box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.08);
			border: none;
			&::before {
				width: 15px;
				height: 15px;
				background: url("../img/icon/top-bar/ruble.svg");
				content: '';
			}
			&::after {
				min-width: 13px;
				height: 7px;
				background: url("../img/icon/arr/arr-select-filter.svg");
				content: '';
			}
		}



		&--sort {

			min-width: 171px;
			.select__header {
				width: 100%;
				&::before {
					width: 19px;
					height: 19px;
					background: url("../img/icon/top-bar/sort.svg");
					content: '';
				}

			}
		}

	}

	&__select-view {

		display: flex;
		gap: 12px;
		align-items: center;
		margin-left: 32px;
		&__column {
			width: 18px;
			height: 18px;
			background: url("../img/icon/top-bar/column-silver.svg");
		}

		&__row {
			width: 21px;
			height: 15px;
			background: url("../img/icon/top-bar/row.svg");
		}

	}


}
